import React,{useState} from "react";
import Grid from '@mui/material/Grid';
import Paper from '@mui/material/Paper';
import CastForEducationIcon from '@mui/icons-material/CastForEducation';
import CodeIcon from '@mui/icons-material/Code';
import PeopleIcon from '@mui/icons-material/People';
import Scroll from "./Scroll";
import "./styleSkills.css"
import SkillBar  from 'react-skillbars';
import { useContext } from "react";
import { LanguageContext } from "../../context/context";

const Skills=()=>{
    const[over,setOver]=useState(false)
    const{language, handleLanguage}=useContext(LanguageContext)

    const skills = [
        { type: 'Javascript', level: 85 },
        { type: 'Typescript', level: 80 },
        { type: 'React', level: 85 },
        { type: 'Vue', level: 80 },
        { type: 'Node Js', level: 75 },
        { type: 'Microsoft .net', level: 75},
        { type: 'MySql', level: 75 },
        { type: 'MongoDB', level: 75 },
        { type: 'Ingles', level: 85 }
      ];
      const colors = {
        bar: "#00000",
        title: {
          text: "#fff",
          background: "#2980b9"
        }
      };

      const handleMouseOver=()=>{
        if(over){
            setOver(false) 
        }else{
            setOver(true)
        }
       
      }


    return(
        <div id="containerSkills">
            <div className="skillsTitleP">
                {language=="spanish" ? 
                <h3 id="skills">Habilidades</h3>
                : 
                <h3 id="skills">Skills</h3>}
          
               
            </div>
           
            <Grid container spacing={2} className="boxSkillsContainer">
           
                <Grid item xs={12} md={4}>
                
                    <div className="boxSkills" onMouseOver={handleMouseOver} onMouseLeave={handleMouseOver}>
                        {language=="spanish" ? 
                        <>
                            <p className="boxSkillsTitle">Desarrollo</p>
                            
                            <p> Manejo de multiples tecnologias y altamente adaptable a cambios. Ingles avanzado </p>
                        </>
                        : 
                        <>
                            <p className="boxSkillsTitle">Development</p>
                            <p> Management of multiple technologies and highly adaptable to changes. Advanced English </p>
                        </>}
                        <CodeIcon style={{height:160,width:"60%"}}id={ over &&"iconOne"} />
                        
                    </div>
                  
                 </Grid>
                
                <Grid item xs={12} md={4}>
                    <div  className="boxSkills" onMouseOver={handleMouseOver} onMouseLeave={handleMouseOver}>
                    {language=="spanish" ?
                     <>
                        <p className="boxSkillsTitle">Habilidades Blandas</p>
                        <p>Mas de 15 años de experiencia en empresas de alto nivel en otras areas. Trabajo en equipo.</p>
                     </>
                    :
                     <>
                        <p className="boxSkillsTitle">Soft Skills</p>
                        <p> More than 15 years of experience in high-level companies in other areas. Teamwork.</p>
                    </>
                    }
                        <PeopleIcon style={{height:160,width:"60%"}}id={ over &&"iconOne"} />
                    </div>
                </Grid>
                <Grid item xs={12} md={4}>
                    <div  className="boxSkills" onMouseOver={handleMouseOver} onMouseLeave={handleMouseOver}>
                    {language=="spanish" ? 
                        <>
                            <p className="boxSkillsTitle">Aprendizaje</p>
                            <p>Predispuesto al aprendizaje de nuevas tecnologias y nuevos metodos de trabajo.</p>
                        </>
                        : 
                        <>
                            <p className="boxSkillsTitle">Learning</p>
                            <p>Predisposed to learning new technologies and new work IT methods.</p>
                        </>
                        }
                        <CastForEducationIcon style={{height:160,width:"60%"}} id={ over &&"iconOne"}/>
                    </div>
                </Grid>

            </Grid>
            <div className="technical">
       <Scroll/>
                {language=="spanish" ? 
                <h3> Tecnologias</h3>
                :
                <h3> Technologies</h3>}
                
            
                </div>
            
        </div>
    )
}

export default Skills

