import React from "react";
import mockProjects from "../mock/Mock";
import { mockProjectsEng } from "../mock/Mock";
import GitHubIcon from '@mui/icons-material/GitHub';
import LanguageIcon from '@mui/icons-material/Language';
import { useContext } from "react";
import { LanguageContext } from "../../context/context";
import Grid from '@mui/material/Grid';
import "./styleCardProject.css"

const CardProject=()=>{
  const{language, handleLanguage}=useContext(LanguageContext)

    return(
     
      <div className="cardProject" id="projects">
         {language=="spanish" ?  
            <h3 className="projectTitle">Proyectos</h3>
            : 
            <h3 className="projectTitle">Projects</h3>
            }
           
         <Grid container spacing={0} >
            {language=="spanish" ? (
              mockProjects.map((item)=>{
                  return(
            <Grid item xs={12} md={4}>
              <div class="scenes" tabindex="0" >
                <div class="scene-1" style={{backgroundImage:`url(${item.img})`,backgroundPosition:'center',backgroundRepeat: 'no-repeat'}}>
                  
                  
                </div>
                
                <div class="scene-2" style={{  /*backgroundImage:`url(${item.img})`,backgroundPosition:'center',backgroundRepeat: 'no-repeat',*/}}>
                <div className="scene2Text">
                  <h2 className="scene-titl">{item.title}</h2>
                  <div className="link">
                    <h2 className="scene-title-link" onClick={()=>window.open(item.linkGit)} >Code</h2>
                    <h2 onClick={()=>window.open(item.linkSite)} >Website</h2>
                  </div>
                  </div>
                </div>
                
              </div>
              <div className="textCardProject">
                <p style={{color:"white", maxWidth:400}} className="projectP">{item.category} </p>
                <p style={{color:"white", maxWidth:400}} className="projectP">{item.tech} </p>
              </div>
            </Grid>
            )
            })

            ):(
              mockProjectsEng.map((item)=>{
                return(
                <Grid item xs={12} md={4} >
                  <div class="scenes" tabindex="0">
                  <div class="scene-1"style={{backgroundImage:`url(${item.img})`,backgroundPosition:'center',backgroundRepeat: 'no-repeat'}}>
                    <h2 class="scene-title">{item.title}</h2>
                  </div>
                  <div class="scene-2" >
                    <h2 className="scene-titl">{item.title}</h2>
                    <div className="link">
                      <h2 className="scene-title-link" onClick={()=>window.open(item.linkGit)} >Code</h2>
                      <h2 onClick={()=>window.open(item.linkSite)} >Website</h2>
                    </div>
                  </div>
                  
                </div>
                <div className="textCardProject">
                  <p style={{color:"white", maxWidth:400}} className="projectP">{item.category} </p>
                  <p style={{color:"white", maxWidth:400}} className="projectP">{item.tech} </p>
                </div>
                </Grid>
               )
              })

            )}
        </Grid>
      </div>
      
    )
}

export default CardProject

